@import url(https://fonts.googleapis.com/css?family=Hind:300,400,500,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Blinker:100,200,300,400,600,700,800&display=swap);
.lists {
  width: 100%;
  height: 100vh;
  margin-top: 100px; }

.project-list {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap; }
  .project-list a {
    text-decoration: none; }

.summary-container {
  padding: 10%; }

.project-summary {
  width: 250px;
  height: 350px;
  border: 1px solid #b4b5b9;
  margin: 0 20px 50px 20px;
  -webkit-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in; }
  .project-summary .summary-day {
    color: #f39d56;
    margin-bottom: 10px; }
  .project-summary .summary-title {
    height: 150px;
    display: block;
    font-size: 30px;
    color: white;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 10px; }
  .project-summary .summary-content {
    widows: 100%;
    height: 95px;
    overflow: hidden;
    white-space: pre-wrap;
    text-overflow: ellipsis;
    text-align: center;
    color: #b4b5b9; }

.project-summary:hover {
  background: white; }
  .project-summary:hover .summary-title {
    color: #96979c; }

nav {
  position: absolute;
  right: 5%;
  width: 100px;
  height: 30vh;
  background: none; }
  nav .navContainer {
    color: white; }
    nav .navContainer h2 {
      font-size: 20px;
      font-weight: 600;
      margin-bottom: 20px;
      color: white;
      padding: 5px;
      -webkit-transition: all 0.2s ease-in;
      transition: all 0.2s ease-in; }
    nav .navContainer h2:hover {
      color: white;
      background: #9347f7; }
  nav .loginMenu h3 {
    width: 100%;
    padding: 5px;
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 20px;
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
    cursor: pointer; }
    nav .loginMenu h3 a {
      color: white;
      text-decoration: none; }
  nav .loginMenu h3:hover {
    background: #9347f7; }

.create-container {
  width: 80vw;
  margin: auto;
  height: 90vh; }
  .create-container form {
    position: relative;
    width: 80%;
    height: 100%;
    margin: auto;
    padding: 3%;
    margin-top: 3%; }
    .create-container form h1 {
      font-size: 50px;
      color: #9347f7;
      margin-bottom: 5%;
      letter-spacing: 1px; }
    .create-container form .input-field {
      display: flex;
      flex-direction: column; }
      .create-container form .input-field label {
        color: white;
        font-size: 20px;
        letter-spacing: 1px; }
      .create-container form .input-field input {
        margin-top: 2%;
        padding: 1%;
        width: 60%;
        height: 20px;
        border: none;
        border-bottom: 1px solid #9347f7;
        outline-color: #9347f7;
        border-color: #9347f7;
        margin-bottom: 3%;
        background: #25262a;
        color: white; }
      .create-container form .input-field textarea {
        width: 90%;
        margin-top: 2%;
        padding: 1%;
        height: 450px;
        outline-color: #9347f7;
        background: #25262a;
        color: #b4b5b9;
        margin-bottom: 3%; }
      .create-container form .input-field button {
        width: 20%;
        right: 12.5%;
        position: absolute;
        padding: 1%;
        background: white;
        color: #f39d56;
        border: none;
        font-size: 20px;
        font-weight: bold;
        letter-spacing: 1px;
        -webkit-transition: all 0.3s ease-in;
        transition: all 0.3s ease-in; }
      .create-container form .input-field button:hover {
        background: #f39d56;
        color: white;
        cursor: pointer; }

.login-container,
.signup-container {
  margin-top: 5%; }
  .login-container form,
  .signup-container form {
    margin: auto;
    width: 50vw;
    height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center; }
    .login-container form h1,
    .signup-container form h1 {
      margin: 50px 0;
      font-size: 40px;
      letter-spacing: 1px;
      color: #9347f7; }
    .login-container form .input-field,
    .signup-container form .input-field {
      display: flex;
      flex-direction: column;
      width: 70%; }
      .login-container form .input-field label,
      .signup-container form .input-field label {
        color: #d4d4d4;
        margin-bottom: 3%; }
      .login-container form .input-field input,
      .signup-container form .input-field input {
        margin-bottom: 10%;
        padding: 2%;
        background: #25262a;
        border: none;
        border-bottom: 1px solid #9347f7;
        outline-color: #9347f7;
        color: white; }
    .login-container form .user,
    .signup-container form .user {
      color: #75fc36;
      letter-spacing: 1px;
      text-align: center; }
      .login-container form .user h3,
      .signup-container form .user h3 {
        margin-bottom: 20px; }
      .login-container form .user p,
      .signup-container form .user p {
        margin-bottom: 10px; }
    .login-container form button,
    .signup-container form button {
      width: 80%;
      padding: 1%;
      margin: auto;
      margin-top: 10%;
      margin-bottom: 10%;
      background: white;
      color: #f39d56;
      border: none;
      font-size: 20px;
      font-weight: bold;
      letter-spacing: 1px;
      -webkit-transition: all 0.3s ease-in;
      transition: all 0.3s ease-in; }
    .login-container form button:hover,
    .signup-container form button:hover {
      background: #f39d56;
      color: white;
      cursor: pointer; }
  .login-container .error-text,
  .signup-container .error-text {
    margin: auto;
    color: #e72b2b; }

.container.signup {
  margin-top: 50px; }
  .container.signup form {
    margin: auto;
    width: 50vw;
    background: gray;
    display: flex;
    flex-direction: column;
    align-items: center; }
    .container.signup form h5 {
      margin: 50px 0; }
    .container.signup form .input-field {
      width: 70%; }
    .container.signup form button {
      width: 100%;
      margin: 10% 0; }
    .container.signup form button:hover {
      font-weight: bold; }

.dashboardContainer {
  margin: auto;
  margin-top: 50px;
  width: 90vw;
  height: 100vh;
  background: #25262a; }
  .dashboardContainer .profile {
    width: 100%;
    height: 300px;
    display: flex;
    color: white;
    align-items: center; }
    .dashboardContainer .profile .image {
      width: 300px;
      height: 300px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 100px; }
    .dashboardContainer .profile img {
      width: 100%; }
    .dashboardContainer .profile h1 {
      font-size: 100px;
      margin-bottom: 20px; }
    .dashboardContainer .profile p {
      font-size: 30px;
      color: #9347f7; }

.project-details {
  width: 90vw;
  height: 100%;
  margin: auto; }
  .project-details .detail-cotainer {
    margin: auto;
    margin-top: 5%;
    padding: 3%;
    width: 70%;
    height: 100vh; }
    .project-details .detail-cotainer h1 {
      font-size: 80px;
      font-weight: 500;
      margin-bottom: 7%;
      color: white; }
    .project-details .detail-cotainer h2 {
      color: #f39d56;
      font-weight: 600;
      margin-bottom: 2%; }
    .project-details .detail-cotainer h3 {
      color: white;
      font-weight: 500;
      letter-spacing: 1px; }
      .project-details .detail-cotainer h3 span {
        color: #9347f7; }
    .project-details .detail-cotainer p {
      width: 70%;
      height: 55%;
      margin-left: 20%;
      font-size: 20px;
      margin-bottom: 10%;
      color: #d4d4d4; }

* {
  font-family: 'Blinker', sans-serif;
  margin: 0;
  padding: 0; }

body {
  background: #25262a; }

